import { Text, Heading } from '@rapid-web/ui';
import { useLoaderData, Outlet } from '@remix-run/react';
import { type LoaderFunction, json } from '@remix-run/node';
import { posts } from '../posts';
import type { BlogPost } from '../posts';
import DarrenProfile from '../../assets/profile.svg';
import Markdown from '~/components/Markdown';

export const loader: LoaderFunction = async ({ request }) => {
	const url = new URL(request.url);
	const pathname = url.pathname;

	const currentPost = posts.find(({ pathName }) => pathName === pathname);

	return json(currentPost);
};

const _mdx = () => {
	const currentPost = useLoaderData<BlogPost>();
	return (
		<div>
			<div className='flex flex-col pb-12'>
				<div className='flex flex-col justify-center mb-14 gap-6'>
					{currentPost?.img && (
						<img
							src={currentPost.img}
							className='w-full shadow-navbarSmall rounded-2xl'
							alt='Blog post cover'
						/>
					)}
					<Text styles='font-light font-secondary text-md text-white mt-6 exclude-from-markdown gradient-text'>
						{currentPost.publishDate}
					</Text>
					<div className='flex flex-col'>
						<Heading styles='exclude-from-markdow font-archivo heading font-bold text-[1.65rem] sm:text-3xl lg:text-4xl text-white'>
							{currentPost.title}
						</Heading>
						<Text styles='font-light text-md text-white mt-4 exclude-from-markdown'>
							{currentPost.description}
						</Text>
					</div>
					<div className='flex items-center gap-2'>
						<img
							width={35}
							height={35}
							className='rounded-full'
							src={DarrenProfile}
							alt={currentPost.author}
						/>
						<Text styles='text-white font-light exclude-from-markdown'>
							{currentPost.author}
						</Text>
					</div>
				</div>
				<Markdown>
					<Outlet />
				</Markdown>
			</div>
		</div>
	);
};

export default _mdx;
